import { Chip } from 'material-ui';
import React, {useEffect, useLayoutEffect} from 'react';
import styles from './Emotions.css';
import EmotionChip from '../containers/EmotionChip';

const Emotions = ({data}) => {
    useLayoutEffect(() => {        
        document.body.className='emotions';
    });

    const onChipClick = (e) => {
        console.log("Chip click", e);
        if(window.sa_event){
            window.sa_event(`Btn-${e}`);
        }
    }
    
    return(
        <div className="emotions-container">
            <h3>How Do You Feel?</h3>
            {data.emotions.map((i,k)=>{ return <EmotionChip label={i.title} icon={i.icon} iconFirst={false} url={`/${i.title.toLowerCase()}`} key={k} chipClick={()=>onChipClick(i.title)}/>})}
        </div>
    );
}
export default Emotions;